"use client"

import { useState } from "react"

export default function CompressPDF() {
  const [file, setFile] = useState<File | null>(null)
  const [quality, setQuality] = useState("recommended")
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState("")

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      if (e.target.files[0].type !== "application/pdf") {
        setError("Please upload a PDF file")
        return
      }
      setFile(e.target.files[0])
      setError("")
    }
  }

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault()
    if (e.dataTransfer.files && e.dataTransfer.files[0]) {
      if (e.dataTransfer.files[0].type !== "application/pdf") {
        setError("Please upload a PDF file")
        return
      }
      setFile(e.dataTransfer.files[0])
      setError("")
    }
  }

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault()
  }

  const handleCompress = async () => {
    if (!file) {
      setError("Please upload a PDF file")
      return
    }

    try {
      setLoading(true)
      setError("")

      const formData = new FormData()
      formData.append("file", file)
      formData.append("quality", quality)

      const response = await fetch("/api/compress", {
        method: "POST",
        body: formData,
      })

      if (!response.ok) {
        throw new Error("Failed to compress PDF file")
      }

      const blob = await response.blob()
      const url = window.URL.createObjectURL(blob)
      const a = document.createElement("a")
      a.href = url
      a.download = "compressed.pdf"
      document.body.appendChild(a)
      a.click()
      window.URL.revokeObjectURL(url)
      document.body.removeChild(a)

      setFile(null)
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred")
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold text-gray-900 mb-4">Compress PDF File</h1>
        <p className="text-lg text-gray-600">
          Reduce file size while optimizing for maximal PDF quality.
        </p>
      </div>

      <div className="max-w-3xl mx-auto">
        <div 
          className="bg-white rounded-lg shadow-sm border-2 border-dashed border-gray-300 p-12"
          onDrop={handleDrop}
          onDragOver={handleDragOver}
        >
          <div className="text-center">
            <svg
              className="mx-auto h-12 w-12 text-gray-400"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M9 13h6m-3-3v6m5 5H7a2 2 0 01-2-2V7a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z"
              />
            </svg>
            <div className="mt-4">
              <label htmlFor="file-upload" className="cursor-pointer">
                <span className="mt-2 block text-sm font-medium text-gray-900">
                  Drop PDF file here
                </span>
                <span className="mt-1 block text-sm text-gray-500">
                  or click to select file
                </span>
                <input
                  id="file-upload"
                  name="file-upload"
                  type="file"
                  className="hidden"
                  accept=".pdf"
                  onChange={handleFileChange}
                />
              </label>
            </div>
          </div>

          <div className="mt-6">
            <label htmlFor="compression" className="block text-sm font-medium text-gray-700">
              Compression Level
            </label>
            <select
              id="compression"
              className="mt-1 block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm rounded-md"
              value={quality}
              onChange={(e) => setQuality(e.target.value)}
            >
              <option value="recommended">Recommended compression</option>
              <option value="extreme">Extreme compression</option>
              <option value="less">Less compression</option>
            </select>
          </div>

          {error && (
            <div className="mt-4 p-4 bg-red-50 rounded-md">
              <p className="text-sm text-red-600">{error}</p>
            </div>
          )}

          {file && (
            <div className="mt-6">
              <h3 className="text-lg font-medium text-gray-900 mb-4">Selected File:</h3>
              <div className="flex items-center justify-between py-2 px-4 bg-gray-50 rounded">
                <span className="text-sm text-gray-900">{file.name}</span>
                <span className="text-sm text-gray-500">{(file.size / 1024 / 1024).toFixed(2)} MB</span>
              </div>
              <button
                type="button"
                className={`mt-6 w-full inline-flex justify-center items-center px-6 py-3 border border-transparent text-base font-medium rounded-md text-white ${
                  loading ? "bg-blue-400 cursor-not-allowed" : "bg-blue-600 hover:bg-blue-700"
                }`}
                onClick={handleCompress}
                disabled={loading}
              >
                {loading ? "Compressing..." : "Compress PDF"}
              </button>
            </div>
          )}
        </div>

        <div className="mt-8 text-center">
          <h2 className="text-lg font-medium text-gray-900 mb-4">How to Compress PDF Files</h2>
          <ol className="text-left space-y-4">
            <li className="flex items-start">
              <span className="flex-shrink-0 h-6 w-6 flex items-center justify-center rounded-full bg-blue-100 text-blue-600 font-medium text-sm mr-3">1</span>
              <span className="text-gray-600">Upload the PDF file you want to compress</span>
            </li>
            <li className="flex items-start">
              <span className="flex-shrink-0 h-6 w-6 flex items-center justify-center rounded-full bg-blue-100 text-blue-600 font-medium text-sm mr-3">2</span>
              <span className="text-gray-600">Choose your preferred compression level</span>
            </li>
            <li className="flex items-start">
              <span className="flex-shrink-0 h-6 w-6 flex items-center justify-center rounded-full bg-blue-100 text-blue-600 font-medium text-sm mr-3">3</span>
              <span className="text-gray-600">Click "Compress PDF" and download your optimized PDF</span>
            </li>
          </ol>
        </div>
      </div>
    </div>
  )
}
